import React from "react";
import "./About.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faLinkedinIn, faGithub } from "@fortawesome/free-brands-svg-icons";

export default function About() {
  return (
    <div className="about-container">
      <div className="about text-center">
        <h1 className="about-title">About</h1>
        <div className="card my-card" style={{ width: "20rem" }}>
          <div class="card-body mb-2">
            <h5 class="card-title my-card-title">Pankaj Singh</h5>
            <h6 class="card-subtitle mb-2 text-muted">
              IIT Patna EEE Student :D
            </h6>
            <p class="card-text">
              I'm currently studying in the{" "}
              <span className="text-warning">3rd year of of 4 years</span> my
              B.Tech course in EEE.
            </p>
            <a
              href="https://github.com/pankajsngh2000"
              className="card-link my-link-git"
              target="_blank"
              rel="noreferrer"
            >
              <FontAwesomeIcon icon={faGithub} />
            </a>
            <a
              href="https://www.linkedin.com/in/pankaj-singh-0399301ab"
              className="card-link my-link-ldn"
              target="_blank"
              rel="noreferrer"
            >
              <FontAwesomeIcon icon={faLinkedinIn} />
            </a>
            <a href="/" className="btn btn-success my-btn">
              Download My Resume
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}
