import React from "react";
import "./Home.css";
import Typewriter from "typewriter-effect";

export default function Home() {
  return (
    <div className="home-container">
      <div className="home text-center">
        <h1 className="name neonText">Pankaj Singh</h1>
        <p>
          <span id="typewriter-start">I'm </span>
          <span id="typewriter-end">
            <Typewriter
              onInit={(typewriter) => {
                typewriter
                  .typeString("an Electrical And Electronics Engineer.")
                  .pauseFor(2500)
                  .deleteAll()
                  .typeString("a Web Developer.")
                  .pauseFor(2500)
                  .deleteAll()
                  .typeString("a partime gamer.")
                  .pauseFor(2500)
                  .deleteAll()
                  .start();
              }}
              options={{ cursor: "\\", loop: true }}
            />
          </span>
        </p>
      </div>
    </div>
  );
}
